  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .game__title {
    margin-bottom: 1.8rem;
    font-size: 3.6rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .game__title {
    font-size: 3rem;
    margin-bottom: 2rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .game__title {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
}

.game__counter {
  margin: 0;
  text-transform: uppercase;
  font-family: "Radical", "Arial", sans-serif;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: 0;
}
@media (min-width: 2000px) {
  .game__counter {
    font-size: 48px;
  }
}
@media (min-width: 1024px) {
  .game__counter {
    margin-right: 5.6rem;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .game__counter {
    margin-bottom: 1.8rem;
    font-size: 3.6rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .game__counter {
    font-size: 3rem;
    margin-bottom: 2rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .game__counter {
    font-size: 2rem;
