  }
}

.game__body {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.game__buttons {
  position: fixed;
  left: 0;
  z-index: 100;
  opacity: 0.5;
}

.game__button {
  margin: 0 30px 30px;
  display: block;
}

.intro {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-grow: 1;
  padding: 15.5rem 0 10rem;
  width: 100%;
}
