@media (max-width: 1024px) and (orientation: portrait) {
  .intro__message {
    width: 100%;
    max-width: 100%;
    font-size: 1.6rem;
    line-height: 1.2;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .intro__message {
    width: 100%;
    max-width: 100%;
    font-size: 1.6rem;
    line-height: 1.2;
  }
}
.intro__message p {
  margin: 0;
}
@media (min-width: 2000px) {
  .intro__message svg {
    width: 16px;
    height: 16px;
  }
}
