.form {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.form__label {
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 1.2rem;
  width: 100%;
  text-transform: uppercase;
  text-align: center;
  font-size: 1.6rem;
  line-height: 1.2;
  opacity: 0;
  pointer-events: none;

  @media (min-width: $stop-scaling) {
    margin-bottom: 12px;
    font-size: 16px;
  }

  @media (orientation: portrait) {
    opacity: 1;
    pointer-events: auto;
  }
}

.form__field {
  flex: none;
  margin-right: 3rem;
  padding: 0 5.6rem;
  height: 6rem;
  width: 0;
  text-transform: uppercase;
  font: inherit;
  font-size: 2.4rem;
  line-height: 1.2;
  border: 0;
  border-radius: 3rem;
  outline: none;
  flex-grow: 0;
  opacity: 0;
  transition-property: flex-grow, opacity;
  transition-duration: 0.4s, 0.4s;
  transition-timing-function: ease, ease;
  transition-delay: 0.5s, 0.5s;
  color: $c-dark;

  &::placeholder {
    color: transparent;
    transition-property: color;
    transition-duration: 0.4s;
    transition-timing-function: ease;
    transition-delay: 0.8s;
  }

  .active &,
  .screen--show & {
    opacity: 1;
    flex-grow: 1;

    &::placeholder {
      color: $c-grey;
    }
  }

  .result--prize & {
    transition-delay: 1s, 1s;
    &::placeholder {
      transition-delay: 1.4s;
    }
  }

  &:focus {
    border: 1px solid $c-purple;
  }

  @media (min-width: $stop-scaling) {
    margin-right: 30px;
    padding: 0 56px;
    height: 60px;
    width: 0;
    max-width: calc(100% - 210px);
    font-size: 24px;
    border-radius: 30px;
  }

  @media (orientation: portrait) {
    margin-right: 1rem;
    padding: 0 2.5rem;
    height: 4rem;
    font-size: 1.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    margin-right: 1rem;
    padding: 0 2.5rem;
    height: 4rem;
    font-size: 1.6rem;
  }
}

.form__button {
  opacity: 1;
  color: transparent;
  transition-property: color, opacity;
  transition-duration: 0.4s, 0.4s;
  transition-timing-function: ease, ease;
  transition-delay: 0.5s, 0s;

  .result--prize & {
    opacity: 0;
    transition-delay: 1s, 0.8s;
  }

  .active &,
  .screen--show & {
    opacity: 1;
    color: $c-dark;
  }

  .screen--game & {
    width: 18rem;
  }

  @media (orientation: portrait) {
    position: relative;
    flex: none;
    padding: 0;
    width: 4rem;
    height: 4rem;
    font-size: 0;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    position: relative;
    flex: none;
    padding: 0;
    width: 6rem;
    height: 6rem;
    font-size: 0;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    width: 4rem;
    height: 4rem;
  }

  svg {
    @include center;
    display: none;
    width: 1.6rem;
    height: 1.8rem;

    @media (min-width: $stop-scaling) {
      width: 16px;
      height: 18px;
    }

    @media (orientation: portrait) {
      display: block;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      display: block;
    }
  }
}

.form--result {
  .form__field {
    @media (min-width: $stop-scaling) {
      max-width: calc(100% - 310px);
    }

    @media (orientation: portrait) {
    }

    @media (max-width: $tablet) and (orientation: landscape) {
    }

    @media (max-width: $mobile) and (orientation: landscape) {
    }
  }
}
